import { useSelector } from 'react-redux';
import user, { RootState } from '../../../redux/root-reducer';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { useState } from 'react';
import PopoverComponent, { Options } from '../app-popover/popover-component';
import '../chat-view/chatView.scss';
import arrow from '../../../../assets/icons/arrow.svg';
import smiley from '../../../../assets/icons/smiley.svg';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import ProfileDetails from '../app-profile-details/profile-details';
import AppModelService from '../../../_service/app-model-service';
import { RootReducer } from '../../../_models/roo-reducer-model';
import { Avatar } from '@mui/material';

const ChatView = () => {
    const detailedObj: any = useSelector((state: RootReducer) => state.flaggedReport.data);
    const { user } = detailedObj;
    const [pAnchorEl, setpAnchorEl] = useState<EventTarget | any>(null);
    const [popoverOpen, setPopoverOpen] = useState(false);

    const configData = [{ id: 1, text: 'View profile' }]

    const openAction = (event: React.MouseEvent<HTMLElement>) => {
        setPopoverOpen(true);
        setpAnchorEl(event.currentTarget);
    }
    const closePopover = () => {
        setPopoverOpen(false);
    }
    const selectedData = (ev: Options) => {
        AppModelService.createPopUp({
            compoent: <ProfileDetails id={user.id} />,
            headerName: '',
            header: false,
            footer: false,
            width: 580,
            height: 560,
            class: 'main_content',
            showCloseIcon: true
        })
    }


    return (
        <>
            <div className='chatView'>
                <div className='chatContent'>
                    <div className='profile'>
                        <div className='profileInfo'>
                            <Avatar className='profileIcon' src={user.profilePic} alt={user?.name} ></Avatar>
                            <p className='name regular-18'>{user?.name}</p>
                        </div>
                        <div onClick={(e) => openAction(e)}>
                            <MoreVertIcon className='three_dots' />
                        </div>
                    </div>
                    <div className='message-layout'>Conversation
                    <div className='chatWindow'>
                        <AddCircleOutlineIcon />
                        <div className='input-container'>
                            <textarea placeholder='Start typing...' className='inputBox' name="input"></textarea>
                            <img className='smiley' src={smiley} alt="smiley" />
                            <img className='arrow' src={arrow} alt="arrow" />
                        </div>

                    </div>
                    </div>
                </div>
            </div>
            <PopoverComponent open={popoverOpen} anchorEl={pAnchorEl} closePopover={closePopover} configData={configData} selectedData={selectedData} />

        </>
    )
}

export default ChatView;
